import { applyMiddleware, compose, createStore, combineReducers } from "redux";
import thunk from "redux-thunk";
import blockchainReducerRaffle from "./blockchain/blockchainReducerRaffle";
import dataReducerRaffle from "./data/dataReducerRaffle";

const rootReducer = combineReducers({
  blockchain: blockchainReducerRaffle,
  data: dataReducerRaffle,
});

const middleware = [thunk];
const composeEnhancers = compose(applyMiddleware(...middleware));

const configureStore = () => {
  return createStore(rootReducer, composeEnhancers);
};

const storeRaffle = configureStore();

export default storeRaffle;