const initialState = {
  loading: false,
  totalSupply: 0,
  getMaxEntries: 0,
  getNumTickets: 0,
  currentEntries: 0,

  cost: 0,
  error: false,
  errorMsg: "",
};

const dataReducer = (state = initialState, action) => {
  switch (action.type) {
    case "CHECK_DATA_REQUEST":
      return {
        ...state,
        loading: true,
        error: false,
        errorMsg: "",
      };
    case "CHECK_DATA_SUCCESS":
      return {
        ...state,
        loading: false,
        getPlayerTickets: action.payload.getPlayerTickets,
        getMaxEntries: action.payload.getMaxEntries,
        getWinners: action.payload.getWinners,
        numWinners: action.payload.numWinners,
        raffleLive: action.payload.raffleLives,
        getNumTickets: action.payload.getNumTickets,
        currentEntries: action.payload.currentEntries,
        error: false,
        errorMsg: "",
      };
    case "CHECK_DATA_FAILED":
      return {
        ...initialState,
        loading: false,
        error: true,
        errorMsg: action.payload,
      };
    default:
      return state;
  }
};

export default dataReducer;
