// log
import store from "../storeRaffle";

const fetchDataRequest = () => {
  return {
    type: "CHECK_DATA_REQUEST",
  };
};

const fetchDataSuccess = (payload) => {
  return {
    type: "CHECK_DATA_SUCCESS",
    payload: payload,
  };
};

const fetchDataFailed = (payload) => {
  return {
    type: "CHECK_DATA_FAILED",
    payload: payload,
  };
};

export const fetchData = () => {
  return async (dispatch) => {
    dispatch(fetchDataRequest());
    try {
      let getPlayerTickets = await store
        .getState()
        .blockchain.smartContract.methods.getPlayerTickets(ethereum.selectedAddress)
        .call();

        let getMaxEntries = await store
        .getState()
        .blockchain.smartContract.methods.getMaxEntries(ethereum.selectedAddress)
        .call();

      let getWinners = await store
        .getState()
        .blockchain.smartContract.methods.getWinners()
        .call();

        let numWinners = await store
        .getState()
        .blockchain.smartContract.methods.numWinners()
        .call();

        let raffleLive = await store
        .getState()
        .blockchain.smartContract.methods.raffleLive()
        .call();

        let getNumTickets = await store
        .getState()
        .blockchain.smartContract.methods.getNumTickets()
        .call();

        let currentEntries = await store
        .getState()
        .blockchain.smartContract.methods.currentEntries(ethereum.selectedAddress)
        .call();

      dispatch(
        fetchDataSuccess({
          getPlayerTickets, getWinners, numWinners, getMaxEntries, raffleLive, getNumTickets, currentEntries,

        })
      );
    } catch (err) {
      console.log(err);
      dispatch(fetchDataFailed("Could not load data from contract."));
    }
  };
};

